import { useCallback, useReducer } from "react";

const formReducer = (state, action) => {
    // gestion de la action
    switch (action.type) {
        case "INPUT_CHANGE":
            let formIsValid = true;
            // Validation pour chaque objet html, il valide avec l'id et le changement d'tat de l'objet
            for (const inputId in state.inputs) {
                if (!state.inputs[inputId]) {
                    continue;
                }
                if (inputId === action.inputId) {
                    formIsValid = formIsValid && action.isValid;
                } else {
                    formIsValid = formIsValid && state.inputs[inputId].isValid;
                }
            }
            // Aprs, il cre une copie de l'tat prcdent et ajoutes le nouveau tat
            return {
                ...state,
                inputs: {
                    ...state.inputs,
                    [action.inputId]: { value: action.value, isValid: action.isValid },
                },
                isValid: formIsValid,
            };
        case "SET_DATA":
            return {
                inputs: action.inputs,
                isValid: action.formIsValid,
            };
        default:
            return state;
    }
};

// Composant
export const useForm = (initialInputs, initialFormValidity) => {
    // Fonction pour tablir l'tat initiale des objets html et envoie les donns  formState
    const [formState, dispatch] = useReducer(formReducer, {
        inputs: initialInputs,
        isValid: initialFormValidity,
    });

    // Fonction pour capturer les valeurs des objets html et envoie les donns  formState
    const inputHandler = useCallback((id, value, isValid) => {
        dispatch({
            type: "INPUT_CHANGE",
            value: value,
            isValid: isValid,
            inputId: id,
        });
    }, []);

    // Fonction pour reinitialiser le formState des objets html et envoie les donns  formState
    const setFormState = useCallback((inputData, formValidity) => {
        dispatch({
            type: "SET_DATA",
            inputs: inputData,
            formIsValid: formValidity,
        });
    }, []);

    return [formState, inputHandler, setFormState];
};
